@import './../common/styles/sass/variables';
@import './../common/styles/sass/mixins';

.contacts {
  background-color: $backgroundColor;
  backdrop-filter: blur(16px);

  .container {
    @include container();

    .grid {
      display: grid;
      gap: 1.75rem;
      grid-template-columns: repeat(9, minmax(0, 1fr));

      .contactsContainer {
        opacity: 1;
        transform: none;
        display: grid;
        grid-column: span 9/span 9;
        @media screen and (min-width: 1024px) {
          grid-column: span 4/span 4;
        }

        .contactInfo {

          .h3 {
            font-size: 22px;
            line-height: 1.4;
            font-weight: 600;
          }

          .emptySpan {
            background-color: rgba(114, 226, 174, 0.2);
            border-radius: 9999px;
            overflow: hidden;
            width: 8rem;
            height: 0.375rem;
            display: inline-block;
            z-index: 10;
            position: relative;
            transform: translate(0, 0);

            //.slider {
            //  @include slider();
            //}
          }

          .contactBlocks {
            margin-top: 1.25rem;

            .contactBlock {
              background-color: $backgroundBlockColor;
              border-radius: 0.5rem;
              border: 1px solid $borderColor;
              backdrop-filter: blur(12px);
              padding: 1.25rem;
              display: flex;
              margin-top: 1.6rem;

              @media screen and (max-width: 1279px) {
                padding: 1rem;
              }

              .icon {
                background-color: $backgroundBlockColor;
                border-radius: 9999px;
                justify-content: center;
                align-items: center;
                flex-basis: 4rem;
                width: 4rem;
                height: 4rem;
                display: inline-flex;
                margin-right: 1rem;
                border: 2px solid rgba(114, 226, 174, 0.2);

                .svg {
                  display: block;
                  vertical-align: middle;
                  width: 4rem;
                  height: 4rem;
                  border-radius: 9999px;
                  border: 0.5rem solid rgba(114, 226, 174, 0.2);
                }
              }

              .contactTitle {
                .h3 {
                  margin-bottom: 0.5rem;
                  font-weight: 500;
                  font-size: 18px;
                  line-height: 1.5;
                }

                .p {
                  margin-bottom: 0;
                }
              }
            }

          }
        }
      }

      .mailContainer {
        display: grid;
        grid-column: span 9/span 9;
        align-items: center;
        opacity: 1;
        transform: none;

        @media screen and (min-width: 1024px) {
          grid-column: span 5/span 5;
        }

        .form {
          background-color: $backgroundBlockColor;
          padding: 1rem;
          border-radius: 0.5rem;
          border-width: 1px;
          border-color: $borderColor;
          backdrop-filter: blur(12px);
          margin-top: -0.375rem;
          @media screen and (min-width: 768px) {
            padding: 1.25rem;
          }

          .space {
            margin-top: calc(1rem * calc(1));
            margin-bottom: calc(1rem * 0);

            label {
              margin-bottom: 0.375rem;
              display: block;
              font-size: 14px;
              line-height: 1.8;
              font-weight: 400;
              color: $aColor;
            }

            .input {
              min-height: 45px;
              width: 100%;
              border-radius: 0.25rem;
              border-width: 1px;
              border-color: rgba(255, 255, 255, 0.1);
              background-color: rgba(15, 23, 42, 0.1);
              color: rgba(191, 190, 203, 1);
              height: 2.5rem;
              padding-left: 1rem;
              padding-right: 1rem;
              transition-property: border-color;
              font-family: inherit;
              font-size: 100%;
              line-height: inherit;
              margin: 0;
              overflow: visible;

              &:focus {
                border-color: $primaryColor;
                outline: none;
              }
            }

            .textarea {
              min-height: 150px;
              vertical-align: middle;
              width: 100%;
              border-radius: 0.25rem;
              border-width: 1px;
              border-color: rgba(255, 255, 255, 0.1);
              background-color: rgba(15, 23, 42, 0.1);
              padding: 0.625rem 1rem;
              color: rgba(191, 190, 203, 1);
              transition-property: border-color;
              resize: vertical;
              font-family: inherit;
              font-size: 100%;
              line-height: inherit;
              overflow: auto;

              &:focus {
                border-color: $primaryColor;
                outline: none;
              }
            }
          }

          .submitBtn {
            background-color: $primaryColor;
            color: $backgroundColor;
            margin-top: calc(1rem * calc(1));
            margin-bottom: calc(1rem * 0);
            padding: 0.625rem 1.25rem;
            @include btnProperties();

            &:hover {
              background: $aColor;
              color: $backgroundColor;
            }
          }
        }
      }
    }
  }
}






